@font-face {
  font-family: 'icomoon';
  src:  url('fonts/icomoon.eot?uh282n');
  src:  url('fonts/icomoon.eot?uh282n#iefix') format('embedded-opentype'),
    url('fonts/icomoon.ttf?uh282n') format('truetype'),
    url('fonts/icomoon.woff?uh282n') format('woff'),
    url('fonts/icomoon.svg?uh282n#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-home:before {
  content: "\e900";
}
.icon-pen:before {
  content: "\e908";
}
.icon-envelop:before {
  content: "\e945";
}
.icon-menu:before {
  content: "\e9bd";
}
